@import url(https://fonts.googleapis.com/css2?family=Gruppo&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,200;1,300;1,400&display=swap);
::selection{
  color: black;
  background: #D093C3;
} 

::-moz-selection 
  {
    color: black;
    background: #D093C3;
  } 

  ::-webkit-scrollbar {
    width: 0.5vmin;
  }
  
  
  ::-webkit-scrollbar-track {
    background: #0c0b0b;
  }

  ::-webkit-scrollbar-thumb {
    background: #555;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: #888;
  }
.App {
  text-align: center;
  background-color: #0c0b0b;
  font-family: 'Poppins', sans-serif;
}


.outerDiv {
  -webkit-animation-name: reduce-brightness;
          animation-name: reduce-brightness;
  -webkit-animation-duration: 300s;
          animation-duration: 300s;
  background-repeat: no-repeat;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes reduce-brightness {
  from {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }
  to {
    -webkit-filter: brightness(20%);
            filter: brightness(20%);
  }
}

@keyframes reduce-brightness {
  from {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }
  to {
    -webkit-filter: brightness(20%);
            filter: brightness(20%);
  }
}

.lightsOff {
  -webkit-filter: brightness(20%);
          filter: brightness(20%);
}


